* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  display:flex;
}
.left {
  width: 40%;
  background: #fff;
  
  
} 
.right {
  width: 60%;
} 
h1 {
  display: flex;
  color: #5c969e;
  margin: 20px 40px;
}
h2 {
  margin: 0 40px;
  width: 80%;
}

.button-icon {
  align-self: center;
  padding: 0 5px;
}

i {
  color: #30475e;
}
.icon {
  color: #557174;
  margin-right: 15px;
}

.icons {
      align-self: center;
      padding: 0 6px;
      color: #5c969e;
}

button {
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  color: #656565;
  cursor: pointer;
  box-shadow: 0 1px 3px rgb(0 0 0 / 14%);
  padding: 10px;
  background: #fff;
  display: flex;
  margin: 15px 0;
  outline: none;
  transition: all 0.3s ease;
 }

button:hover {
   border: 1px solid #656565;
   transform: translateY(-5px);
}

.button-text {
  transform: translateX(5px);
}


.search {
  position: absolute;
  top: 1rem;
  left: 70%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 400px;
  z-index: 2;
}

.search input {
  padding: 0.5rem;
  font-size: 1.5rem;
  width: 100%;
  outline: orangered;
}
.search-input {
  padding: 20px;
}

span {
  text-decoration: underline;
  color: #5c969e;
}

.steps {
  margin: 0 40px;
}

.sections {
  border: #f4f4f4 2px solid;
  border-radius: 5px;
  display: flex;
  margin: 20px 0;
  padding: 15px;
  transition: all 0.3s ease;
}

.sections:hover {
  background: #f4f4f4;
}

.title {
  display: flex;
}
.content {
  padding: 0 10px;
} 




